.mess_Section{
    display: grid;
    background: #555555;
    grid-template-columns: 1fr 1fr;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
.mess-List_Mess{
    display: grid;
    grid-template-rows: auto 4fr 1fr;
    grid-template-areas:
            'sett'
            'ava'
            'prod';
}
.mess_List_Desc{
    border: black solid;
    grid-area: sett;
}
.mess_List_List{
    border: black solid;
    grid-area:ava ;
}
.mess_List_Form{
    border: black solid;
    grid-area:prod;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
.mess_List_Dia{
    border: black solid;
    display: grid;
    grid-template-rows: 20% auto ;
}
.mess_List_Dia_Panel{
    border: black solid;
}
.mess_List_Dia_List{
    border: black solid;
}
/*@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@@*/
#mess_img{
    width: 100%;
}